/*eslint-disable */
import axios from 'axios';
import { showAlert } from './alert';

export const login = async (email, password) => {
  try {
    const res = await axios({
      method: 'POST',
      url: 'http://127.0.0.1:3008/api/v1/users/login',
      data: {
        email,
        password,
      },
    });

    if (res.data.status === 'success') {
      showAlert('success', 'You are now logged in!');
      location.assign('/');
    }
  } catch (err) {
    showAlert('error', err.response.data.message);
  }
};

export const logout = async () => {
  try {
    const res = await axios({
      method: 'GET',
      url: 'http://127.0.0.1:3008/api/v1/users/logout',
    });
    if (res.data.status === 'success') {
      showAlert('success', 'You are now logged out!');
      location.reload(true);
    }
  } catch (err) {
    showAlert('error', 'Error, try logging out again.');
    console.log(err);
  }
};
